.container { width: 100vw; height: 100vh; background-color: grey; }

.setting {position: absolute; height: 100px; width: 600px; background-color: #FFFFFFDD; left: calc(50vw - 300px); border: 1px solid #00000088;}
.setting-top {top: 0px;}
.setting-bottom {bottom: 0px; display: none;}
.setting * {color:black;}
.setting-inner {position: relative; display: flex; justify-content: space-around; width: calc(100% - 40px); padding: 20px; height: calc(100% - 40px);}

.setting-top .set-item {margin: 10px auto; width: 40px; height: 40px; border-radius: 50%; cursor: pointer; transition: all 0.2s; border: 1px solid grey;}
.set-item:hover {transform: scale(1.1);}
.set-item.active {transform: scale(1.05); box-shadow: 0 0 10px #00000088;}

.setting-bottom .set-item {margin: 10px auto; height: 40px; width: auto; cursor: pointer; transition: all 0.2s;}
